.row {
    display: flex;
    flex-wrap: wrap;
    margin-left: -10px;
    margin-right: -10px; }

.col-1 {
    width: 8.333333%; }

.col-2 {
    width: 16.666667%; }


.col-3 {
    width: 25%;
    @include md {
        width: auto; } }

.col-4 {
    width: 33.333333%; }

.col-5 {
    width: 41.666667%; }

.col-6 {
    width: 50%;
    @include md {
        width: 100%; } }

.col-7 {
    width: 58.333333%; }

.col-8 {
    width: 66.666667%; }


.col-9 {
    width: 75%; }


.col-10 {
    width: 83.333333%; }


.col-11 {
    width: 91.666667%; }


.col-12 {
    width: 100%; }


.col-1, .col-2, .col-3, .col-4, .col-5, .col-6, .col-7, .col-8, .col-9, .col-10, .col-11, .col-12, .col-sm-6 {
    display: flex;
    padding-left: 10px;
    padding-right: 10px;
    box-sizing: border-box; }
@include md {
  .col-sm-1 {
    width: 8.333333%; }

  .col-sm-2 {
    width: 16.666667%; }

  .col-sm-3 {
    width: 25%; }

  .col-sm-4 {
    width: 33.333333%; }

  .col-sm-5 {
    width: 41.666667%; }

  .col-sm-6 {
    width: 50%; }

  .col-sm-7 {
    width: 58.333333%; }

  .col-sm-8 {
    width: 66.666667%; }

  .col-sm-9 {
    width: 75%; }

  .col-sm-10 {
    width: 83.333333%; }

  .col-sm-11 {
    width: 91.666667%; }

  .col-sm-12 {
    width: 100%; } }
@include sm {
    .col-xs-1 {
        width: 8.333333%; }

    .col-xs-2 {
        width: 16.666667%; }


    .col-xs-3 {
        width: 25%; }

    .col-xs-4 {
        width: 33.333333%; }

    .col-xs-5 {
        width: 41.666667%; }

    .col-xs-6 {
        width: 50%; }


    .col-xs-7 {
        width: 58.333333%; }


    .col-xs-8 {
        width: 66.666667%; }


    .col-xs-9 {
        width: 75%; }


    .col-xs-10 {
        width: 83.333333%; }

    .col-xs-11 {
        width: 91.666667%; }


    .col-xs-12 {
        width: 100%; } }
