//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }

$familyGilroy: 'Gilroy';

@mixin gilroy {
	font-family: 'Gilroy', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif; }


// Gilroy
@font-face {
  @include fontdefault(normal, 300, $familyGilroy);
  @include fontpath('Gilroy-Light', 'Gilroy-Light', Gilroy-Light); }

@font-faces {
	@include fontdefault(normal, 400, $familyGilroy);
	@include fontpath('Gilroy', 'Gilroy', Gilroy); }
@font-face {
	@include fontdefault(italic, 400, $familyGilroy);
	@include fontpath('Gilroy-Italic', 'Gilroy-Italic', Gilroy-Italic); }
@font-face {
	@include fontdefault(normal, 700, $familyGilroy);
	@include fontpath('Gilroy-Bold', 'Gilroy-Bold', Gilroy-Bold); }
@font-face {
	@include fontdefault(italic, 700, $familyGilroy);
	@include fontpath('Gilroy-BoldItalic', 'Gilroy-BoldItalic', Gilroy-BoldItalic); }
