.hero-bg-inner {
    height: 100%;
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    margin: 0 -40px;
    &__container {
        width: 900px;
        height: 804px;
        position: absolute;
        bottom: 0;
        max-width: 100%;
        @include xl {
            right: -150px; }
        @include lg {
            right: -250px; }
        @include md {
            right: -400px;
            overflow: hidden; }
        @include sm {
            width: auto; } }
    &__img {
        background-image: url(../img/bg-header.png);
        background-repeat: no-repeat;
        background-size: contain;
        background-position: right bottom;
        background-size: contain;
        width: 100%;
        height: 100%; } }
